<template>
  <div class="order">
    <!-- 我的订单主要内容 -->
    <div class="order-content" v-if="orders!==null">
      <div class="content" v-for="(item,index) in orders" :key="index">
        <ul>
          <!-- 我的订单表头 -->
          <li class="order-info">
            <div class="order-id">订单编号: {{orders[index].orderItemId}}</div>
            <div class="order-time">订单时间: {{orders[index].createTime | dateFormat}}</div>
            
          </li>
          <li class="header">
            <div class="pro-img"></div>
            <div class="pro-name">商品名称</div>
            <div class="pro-price">单价</div>
            <div class="pro-num">数量</div>
            <div class="pro-total">小计</div>
            <div class="pro-operate">操作</div>
 
          </li>
          <!-- 我的订单表头END -->

          <!-- 订单列表 -->
          <li class="product-list"  >
            <div class="pro-img">
              <router-link :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }">
                <img :src="orders[index].goodsCoverImg" />
              </router-link>
            </div>
            <div class="pro-name">
              <router-link
                :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }"
              >{{orders[index].goodsName}}</router-link>
            </div>
            <div class="pro-price">{{orders[index].sellingPrice}}元</div>
            <div class="pro-num">{{orders[index].goodsCount}}</div>
            <div class="pro-total pro-total-in">{{orders[index].sellingPrice*orders[index].goodsCount}}元</div>
          
            <div class="pro-operate">
               
             
                <el-dialog title="表单弹框" :visible.sync="dialogVisible" width="30%">
                  <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="商品名" >
                      <el-input v-model="form.goodsName" ></el-input>
                    </el-form-item>
                    <el-form-item label="分类">
                    <el-select v-model="form.categoryId" >
                        <el-option label="丹药" value=15></el-option>
                        <el-option label="炼丹炉" value="16"></el-option>
                        <el-option label="功法" value="17"></el-option>
                        <el-option label="异火" value="18"></el-option>
                        <el-option label="武器" value="19"></el-option>
                        <el-option label="灵宠" value="20"></el-option>
                        <el-option label="其他" value="21"></el-option>
                    </el-select>
                    </el-form-item>

                 
                    <el-form-item label="售价">
                      <el-input v-model="form.sellingPrice"></el-input>
                    </el-form-item>
                    <el-form-item label="总数">
                      <el-input v-model="form.goodsCount"></el-input>
                    </el-form-item>
                    <el-form-item label="状态">
                        <el-select v-model="form.orderStatus"   >
                            <el-option label="待支付" value=0></el-option>
                            <el-option label="已支付" value=1></el-option>
                            <el-option label="已发货" value=2></el-option>
                            <el-option label="已签收" value=3></el-option>
                        </el-select>
                    </el-form-item>
                  </el-form>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="undateOrder()">确 定</el-button>
                  </span>
                </el-dialog>
               
 
              <el-button type="primary" size="mini"   @click="delOrder(orders[index].orderItemId)">删除</el-button>
              <el-button type="success" size="mini"   @click="undateOrderID( index )">修改</el-button>
              <!-- <el-button type="success" size="mini" v-show="orders[index].orderStatus == 0" @click="addOrder(orders[index].orderItemId)">添加</el-button> -->
             
            </div>
    </li>
        </ul>
          <div class="order-bar">
            <!-- <div class="order-bar-left">
              <span class="order-total">
                共
                <span class="order-total-num">{{total[index].totalNum}}</span> 件商品
              </span>
            </div> -->
            <div class="order-bar-right">
              <span>
                <span class="total-price-title">状态：</span>
                <span class="total-price">{{orderst[orders[index].orderStatus]}}  </span>
              </span>
              <br>
              
            </div>
          </div>
 
          <!-- 订单列表END -->
        </div>
      </div>
      <div style="margin-top:-40px;"></div>
    </div>
    <!-- 我的订单主要内容END -->

    <!-- 订单为空的时候显示的内容 -->
    <!-- <div v-else class="order-empty">
      <div class="empty">
        <h2>您的订单还是空的！</h2>
        <p>快去购物吧！</p>
      </div>
    </div> -->
    <!-- 订单为空的时候显示的内容END -->
  <!-- </div> -->
</template>
<script>
import { mapGetters } from "vuex";

export default {
    computed: {
    ...mapGetters([
      "getOrder",
    ]),
  },
  // 0待支付 1已支付 2已发货 3已签收 
  data() {
    
    return {
        changeID:0,
        dialogVisible:false,
        form: {
          goodsName: '',
          categoryId:'',
          sellingPrice:'',
          goodsCount:'',
          orderStatus:'',
        },
      orderst:["待支付","已支付","已发货","已签收"],
      forPay:true,
      orders: [], // 订单列表
      total: [] // 每个订单的商品数量及总价列表
    };
  },
  activated() {
     
  },
 
  created(){
    this.indexorder();
  },
  watch: {
      changeID: function(val){

          console.log("watch changeID",this.form.goodsName);
          if(val>= 0){
              this.form.goodsName = this.orders[val].goodsName,
              this.form.categoryId= this.orders[val].categoryId,
              this.form.sellingPrice= this.orders[val].sellingPrice,
              this.form.goodsCount= this.orders[val].goodsCount,
              this.form.orderStatus= this.orders[val].orderStatus ,
           this.dialogVisible = true
          }
      }
 
  },
  methods:{
    undateOrderID(val){
        this.changeID=val 
       
    },
    undateOrder(){
        console.log(this.form.goodsName,this.form.categoryId,this.form.sellingPrice,this.form.goodsCount,this.form.orderStatus,);
       this.dialogVisible = false
       
       this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/admin/ordersInfo/update",
              method: 'post',
              params:{
                orderItemId:this.orderst[this.changeID].goodsId ,
                goodsName:this.form.goodsName,
                categoryId:this.form.categoryId,
                sellingPrice:this.form.sellingPrice,
                goodsCount:this.form.goodsCount,
                orderStatus:this.form.orderStatus,
              }
        })
        .then(res => {
          if(res.data.flag){
                this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("更新订单失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); this.changeID=-1;  this.$router.go(0)
    },
    delOrder(val){
        console.log(val);
       this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/admin/ordersInfo/delete",
              method: 'psot',
              params:{
                orderItemId:val,
              }
        })
        .then(res => {
          if(res.data.flag){
               this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("删除失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
        this.$router.go(0)
    },
    indexorder(){
     this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/admin/ordersInfo",
              method: 'get',
  
        })
        .then(res => {
          if(res.data.flag){
              this.orders = res.data.data;
              console.log(this.orders);
            }
            else{
              this.notifyError("获取购物车的所有商品",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
    }

  }
};
</script>
<style scoped>
.order {
  background-color: #f5f5f5;
  padding-bottom: 20px;
}
/* 我的订单头部CSS */
.order .order-header {
  height: 64px;
  border-bottom: 2px solid #ff6700;
  background-color: #fff;
  margin-bottom: 20px;
}
.order .order-header .order-header-content {
  width: 1225px;
  margin: 0 auto;
}
.order .order-header p {
  font-size: 28px;
  line-height: 58px;
  float: left;
  font-weight: normal;
  color: #424242;
}
/* 我的订单头部CSS END */
.order .content {
  width: 1225px;
  margin: 0 auto;
  background-color: #fff;
  margin-bottom: 50px;
}

.order .content ul {
  background-color: #fff;
  color: #424242;
  line-height: 85px;
}
/* 我的订单表头CSS */
.order .content ul .order-info {
  height: 60px;
  line-height: 60px;
  padding: 0 26px;
  color: #424242;
  border-bottom: 1px solid #ff6700;
}
.order .content ul .order-info .order-id {
  float: left;
  color: #ff6700;
}
.order .content ul .order-info .order-time {
  float: right;
}

.order .content ul .header {
  height: 85px;
  padding-right: 26px;
  color: #424242;
}
/* 我的订单表头CSS END */

/* 订单列表CSS */
.order .content ul .product-list {
  height: 85px;
  padding: 15px 26px 15px 0;
  border-top: 1px solid #e0e0e0;
}
.order .content ul .pro-img {
  float: left;
  height: 85px;
  width: 120px;
  padding-left: 80px;
}
.order .content ul .pro-img img {
  height: 80px;
  width: 80px;
}
.order .content ul .pro-name {
  float: left;
  width: 200px;
}
.order .content ul .pro-name a {
  color: #424242;
}
.order .content ul .pro-name a:hover {
  color: #ff6700;
}
.order .content ul .pro-price {
  float: left;
  width: 50px;
  padding-right: 18px;
  text-align: center;
}
.order .content ul .pro-operate {
  float: left;
  width: 200px;
  padding-left: 100px;
  text-align: center;
}
.order .content ul .pro-num {
  float: left;
  width: 190px;
  text-align: center;
}
.order .content ul .pro-total {
  float: left;
  width: 160px;
  padding-right: 81px;
  text-align: right;
}
.order .content ul .pro-total-in {
  color: #ff6700;
}

.order .order-bar {
  width: 1185px;
  padding: 0 20px;
  border-top: 1px solid #ff6700;
  height: 50px;
  line-height: 50px;
  background-color: #fff;
}
.order .order-bar .order-bar-left {
  float: left;
}
.order .order-bar .order-bar-left .order-total {
  color: #757575;
}
.order .order-bar .order-bar-left .order-total-num {
  color: #ff6700;
}
.order .order-bar .order-bar-right {
  float: right;
}
.order .order-bar .order-bar-right .total-price-title {
  color: #ff6700;
  font-size: 14px;
}
.order .order-bar .order-bar-right .total-price {
  color: #ff6700;
  font-size: 30px;
}
/* 订单列表CSS END */

/* 订单为空的时候显示的内容CSS */
.order .order-empty {
  width: 1225px;
  margin: 0 auto;
}
.order .order-empty .empty {
  height: 300px;
  padding: 0 0 130px 558px;
  margin: 65px 0 0;
  background: url(../assets/imgs/cart-empty.png) no-repeat 124px 0;
  color: #b0b0b0;
  overflow: hidden;
}

.order .order-empty .empty h2 {
  margin: 70px 0 15px;
  font-size: 36px;
}
.order .order-empty .empty p {
  margin: 0 0 20px;
  font-size: 20px;
}
/* 订单为空的时候显示的内容CSS END */
</style>